
import { defineComponent, PropType } from "vue";
import StarRating from "@/components/reusable/StarRating.vue";

export default defineComponent({
  name: "ApartmentItem",
  components: {
    StarRating,
  },
  props: {
    imgUrl: {
      type: String as PropType<string>,
      default: "",
    },
    description: {
      type: String as PropType<string>,
      required: true,
    },
    rating: {
      type: Number as PropType<number>,
      default: 0,
    },
    price: {
      type: Number as PropType<number>,
      required: true,
    },
    id: {
      type: String as PropType<string>,
      required: true,
    },
  },
});
